.header{

    background-color: #003580;
    color: white;
    display: flex;
    justify-content: center;
    position: relative;
}

.headerContainer{

    width: 100%;
    max-width: 1024px;
    
}

.headerContainer.listMode{

    margin: 20px 0px 0px 0px;
}


.headerList{

    display: flex;
    gap: 40px;
    margin-bottom:50px ;
}

.headerListItem{
    display: flex;
    justify-content: center;
    gap: 10px;
    padding: 10px;
    border-radius: 20px;
    
}

.headerListItem.active{

    border: 1px solid white;
}

.headerDesc{

    margin: 20px 0px;
}

.headerBtn{

    background-color: #0757c7;
    color: white;
    font-weight: 500px;
    border: none;
    padding: 10px;
    cursor: pointer;
}

.headerSearch{

    height: 30px;
    background-color: white;
    border: 3px solid #febb02;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 10px 0px;
    border-radius: 5px;
    position: absolute;
    bottom: -25px;
    width: 100%;
    max-width: 1024px;

}

.headerIcon{
    color: rgb(141, 134, 134);
}

.headerSearchItem{

    display: flex;
    align-items: center;
    gap: 10px;
}

.headerSearchInput{

    border: none;
    outline: none;
}

.headerSearchText{

    color: rgb(150, 150, 150);
    cursor: pointer;
}

.date{

    position: absolute;
    top:50px;
    z-index: 2;
}

.options{

    position: absolute;
    z-index: 2;
    top: 50px;
    background-color: white;
    color: gray;
    border-radius: 5px;
    -webkit-box-shadow:0px 0px 10px -5px black;
    box-shadow: 0px 0px 10px -5px black;
}

.optionItem{

    width: 200px;
    display: flex;
    justify-content: space-between;
    margin: 10px;
}


.optonCounter{

    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 12px;
    color: black;
}

.optionConterButton{

    width: 30px;
    height: 30px;
    border: 1px solid #0071c2 ;
    color: #0071c2;
    cursor: pointer;
    background-color: white;
}

.optionConterButton:disabled{

    cursor: not-allowed;
}