.serchItem{

    
    border: 1px solid lightgray; 
    border-radius: 5px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;

}

.siImg{

    width: 200px;
    height: 200px;
    object-fit: cover;
}

.siDesc{

    display: flex;
    flex-direction: column;
    gap: 10px;
    flex: 2;
}

.siTitle{

    font-size: 20px;
    color: rgb(75, 75, 253);
}


.siDistance{

    font-size: 12px;
}

.siStaxOp{

    border-radius: 5px;
    font-size: 12px;
    background-color: #008009;
    color: wheat;
    padding: 10px;
    width: max-content;

}

.siSubtitle{

    font-size: 12px;
    font-weight: bold;
}

.siFeautures{

    font-size: 12px;
}

.siCancelOp{

    font-size: 14px;
    color: green;
    font-weight: bold;
}
.siDetails{

    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content:space-between ;

}

.siRating{

    display: flex;
    justify-content:space-between ;
}

.siRating>span{

    font-weight: 500;
}

.siRating>button{

    background-color: rgb(72, 72, 255);
    border: none;
    color: white;
    padding: 5px;
    font-weight: bold;
    border-radius: 5px;
}

.siDetailText{

    text-align: right;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.siPrice{

    font-size: 24px;
}

.siTaxOp{

    font-size: 12px;
    color: gray;
}


.siCheckButton{

    background-color: rgb(72, 72, 255);
    border-radius: 5px;
    border: none;
    color: white;
    padding: 10px;
    font-size: 14px;
    cursor: pointer;


}